.blog-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 24px;
}

.blog-post {
  margin-bottom: 40px;
  text-align: center;
}

.blog-image-wrapper {
  width: 100vw; /* usa a largura da tela */
  display: flex;
  justify-content: center;
  margin-left: calc(-50vw + 50%); /* centraliza fora do container */
  margin-bottom: 16px;
}

.blog-image {
  width: 40vw;
  height: auto;
  display: block;
  margin-bottom: 3rem;
}



.blog-title {
  font-size: 32px;
  font-weight: bold;
  color: #222;
  margin-bottom: 12px;  
  margin-bottom: 3rem;
}

.blog-description-wrapper {
  width: 100vw;
  display: flex;
  justify-content: center;
  margin-left: calc(-50vw + 50%);
  margin-bottom: 16px;
}

.blog-description {
  width: 40vw;
  font-size: 18px;
  color: #555;
  line-height: 1.6;
  text-align: justify;
}



.blog-divider-wrapper {
  width: 100vw;
  display: flex;
  justify-content: center;
  margin-left: calc(-50vw + 50%);
}

.blog-divider {
  border: none;
  border-top: 1px solid #ccc;
  width: 60vw;
  margin: 40px 0;
}
